import React from 'react';
import './pokemon.styles.scss';

const Poke = (props) => {
  return (
    <div className="pokemon">
      <img
        alt="pokemon-img"
        src={`https://pokeres.bastionbot.org/images/pokemon/${props.id}.png`}
      />
      <h2>{props.pokemon.name}</h2>
    </div>
  );
};

export default Poke;
